export default "// Import compiled CSR Svelte app.\r\nimport App from '{{App}}';\r\n\r\n// Clean up the pathname argument.\r\nfunction clPath (str) {\r\n  str = str.replace(/^\\/|\\/$/g, \"\");\r\n  str = str.replace(/\\/{2,}/g, \"/\");\r\n  str = `/${str}`;\r\n  return str\r\n}\r\n\r\n// Mount the application.\r\nnew App({\r\n    target: document.body,\r\n    props: {\r\n      currentRoute: clPath(window.location.pathname)\r\n    }\r\n})";